import React, { Component } from 'react';
import { Link } from 'react-router';

class End extends Component {
    send(){
        if(this.props.score>0){
            this.props.sendData()
        }
        
    }
  render() {
        return ( 
            <div className="menu">
                <div className="content">
                <h1>Twj wynik: {this.props.score}</h1>
                <input type="button" className="start" value="Send to base" onClick={this.send.bind(this)}/>
                <Link to="menu"><input type="button" className="start" value="Back"/></Link>
                </div>
            </div>
        );
  }
}
export default End;


