import { Ship, wynik } from './ship';
import { Meteor,colision } from './meteor'

export let score = 0;
export let alive = !colision;
export let positionX = 0;
export let positionY = 0;
export function galaxy(amount){
    score = 0;
    const canv = document.querySelector('#stars');
    const ctx = canv.getContext('2d');
    let canvH = canv.height = window.innerHeight;
    let canvW = canv.width = window.innerWidth;
    let starsArr = [];
    let meteorsArr = [];
    
   
     
    window.addEventListener('resize', function(){
      canvH = canv.height = window.innerHeight;
      canvW = canv.width = window.innerWidth;
      init(amount);
      ship = new Ship(canvW/2,canvH-50);
      ctx.fillStyle='#fff';
    });

    window.addEventListener('orientationchange', function(){
      canvH = canv.height = window.innerHeight;
      canvW = canv.width = window.innerWidth;
      init(amount);
    });
    
    

    function Star(x,y,size,z){
      this.x = x;
      this.y = y;
      this.size = size;
      
      this.draw = function(){
        ctx.fillRect(this.x,this.y,this.size,this.size);
      }
      
      this.update = function(){
        if(this.size===2){
          this.y+=1;
        }
        if(this.size===3){
          this.y+=.6;
        }
        if(this.size===4){
          this.y+=.4;
        }
        
        if(this.y>canvH){
          this.y = -this.size;
        }
        this.draw();
      }
    }
    function licznik(pkt){
      score+=pkt;
      ctx.font = "16px Ubuntu";
      ctx.fillText(score,canvW-50,24);
    }

    let ship = new Ship(canvW/2,canvH-50);
    let lol = new Meteor(100,100,100); 
    //let starsArr = [];

    function init(amount){
      if (/Mobi/.test(navigator.userAgent)) {
        var meteorSize = 20;
        var meteorAmount = 20;
      }
      else{
        var meteorSize = 30;
        var meteorAmount = 30;
      }
      ctx.fillStyle='#fff';
      starsArr = [];
      meteorsArr = [];
      let MeteorSize = 30;

      for(let i=0;i<amount;i++){
      let sizeM = i%3 + 2;
      let x = Math.random()*canvW;
      let y = Math.random()*canvH; 
  
      starsArr.push(new Star(x,y,sizeM));
      
      }
      for(let i=0;i<meteorAmount;i++){
        let speed = Math.floor((Math.random() * 4) + 2);
        let x = Math.random()*canvW;
        let y = Math.random()*canvH-300; 

        meteorsArr.push(new Meteor(x,y,meteorSize,speed));
      }

    }
  
    function animate(){
      if(!document.querySelector('#stars')) { 
        return; //stop the execution of function
       }
      requestAnimationFrame(animate);
      ctx.clearRect(0,0,canvW,canvH);
      for(let i=0;i<starsArr.length;i++){
        starsArr[i].update();
      }
      for(let i=0;i<meteorsArr.length;i++){
        meteorsArr[i].update();

      }
      
      licznik(1);
      lol.update();
      ship.controls();
      positionX = ship.x;
      positionY = ship.y;
      alive = !colision;
      if(!alive){
        window.location.hash = '#End';
      }
    }

    init(amount);
    animate(); 
    score+=wynik;
  };


