import {positionX, positionY} from './gameGround';
export let colision = false;

export function Meteor(x,y,size,speed){
    if(!document.querySelector('#stars')) { 
        return; //stop the execution of function
        
    }
    function getDistance(x1,y1,x2,y2){
        let xDistance = x2-x1;
        let yDistance = y2-y1;
        
        return Math.sqrt(Math.pow(xDistance,2)+Math.pow(yDistance,2));
    }
    function returner(){
        colision=false;
    }
    this.x = x;
    this.y = y;
    this.speed = speed;
    this.size = size;
    this.distance = 0;
    
    const canv = document.querySelector('#stars');
    const ctx = canv.getContext('2d');
    let canvH = canv.height = window.innerHeight;
    let canvW = canv.width = window.innerWidth;
    ctx.fillStyle='#fff';

    this.draw = function(){
        ctx.beginPath();
        ctx.arc(this.x,this.y,size,0,2*Math.PI);
        ctx.fill();
    }

    this.update = function(){
        this.y+=this.speed;
        if(this.y>canvH+this.size){
            this.y = -this.size;
            this.x = Math.random()*canvW;
        }
        if(getDistance(this.x,this.y,positionX,positionY)<40){
            colision=true;
            setTimeout(returner, 200);
        }
        this.draw();
    }
    
}


