import React, { Component } from 'react';
import * as firebase from 'firebase';
import '../css/App.css';
import Bar from '../components/bar';
import Menu from './menu';



class App extends Component {
  constructor(){
    super();

    this.title = firebase.database().ref().child('hello');
    
    this.state = {
      nick: "Bezimienny",
      score:0,
      welcomer:"Connecting...",
      leaders: [],
    };
  }
  setNick(nickname){
    this.setState({nick:nickname});    
  }
  handleScore(score){
    this.setState({score});
  }
  componentDidMount(){


    this.title.on('value', snap => {
      this.setState({
        welcomer:snap.val()
      });
    });
  }
  sendData(){
    firebase.database().ref('Osoby/'+this.state.nick).set({
      score:this.state.score,
    });
    //firebase.database().ref('Osoby').child(this.state.nick).push( this.state.score );
  }
  
  render() {
    if(this.props.children){
      return(
        <div className="App">
        {React.cloneElement(this.props.children, { sendData:this.sendData.bind(this), handleScore:this.handleScore.bind(this), score: this.state.score, nickName: this.state.nick})}
        </div>
      );
    }
    return (
      <div className="App">
        <Menu setNick={this.setNick.bind(this)} tekst={this.state.welcomer}/>
      </div>
    );
  }
}

export default App;



