import React from 'react';
import ReactDOM from 'react-dom';
import { Router, Route, IndexRoute, hashHistory } from "react-router"
import App from './Cpages/App';
import Game from './Cpages/game';
import Menu from './Cpages/menu';
import End from './Cpages/End';
import Board from './Cpages/board';
import * as firebase from 'firebase';
import registerServiceWorker from './registerServiceWorker';



var config = {
    apiKey: "AIzaSyBqZqebZN9NChMLIbY1xHhGElmvK9nOpBo",
    authDomain: "star-comander.firebaseapp.com",
    databaseURL: "https://star-comander.firebaseio.com",
    projectId: "star-comander",
    storageBucket: "star-comander.appspot.com",
    messagingSenderId: "277891496625"
  };
firebase.initializeApp(config);

ReactDOM.render(
<Router history={hashHistory}>
  <Route path="/" component={App}>
    <Route path="game" component={Game}></Route>
    <Route path="end" component={End}></Route>
    <Route path="board" component={Board}></Route>
    <Route path="menu" component={App}></Route>
  </Route>
</Router>, 
document.querySelector('#root'));
registerServiceWorker();



