import React, { Component } from 'react';
import { Link } from 'react-router';

class Board extends Component {
  render() {
    return (
        <div className="menu">
            <div className="content">
                <h1>Leader Board</h1>
                <Link to="menu"><input type="button" className="start" value="Back"/></Link>
            </div>
        </div>
    );
  }
}

export default Board;


